window.onload = () => {
    //ÂçÅÂÖ≠ËøõÂà∂È¢úËâ≤ÂÄºÁöÑÊ≠£ÂàôË°®ËææÂºè  
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
    let arr = [
        {
            data1: 720,
            data2: 678,
            color: '#7596F5',
            title: 'Âº†‰∏â'
        },
        {
            data1: 342,
            data2: 678,
            color: '#EE7668',
            title: 'ÊùéÂõõ'
        },
        {
            data1: 1000,
            data2: 0,
            color: '#7CBEF8',
            title: 'Áéã‰∫î'
        },
        {
            data1: 111,
            data2: 678,
            color: '#F3A263',
            title: 'ËµµÂÖ≠'
        },
        {
            data1: 111,
            data2: 678,
            color: '#FFC0CB',
            title: 'Èæô‰∏É'
        },
        {
            data1: 0,
            data2: 0,
            color: '#FFC0CB',
            title: 'Âá§ÂÖ´'
        },
    ]
    String.prototype.colorRgb = function (opacity) {
        var sColor = this.toLowerCase();
        if (sColor && reg.test(sColor)) {
            if (sColor.length === 4) {
                var sColorNew = "#";
                for (var i = 1; i < 4; i += 1) {
                    sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
                }
                sColor = sColorNew;
            }
            //Â§ÑÁêÜÂÖ≠‰ΩçÁöÑÈ¢úËâ≤ÂÄº  
            var sColorChange = [];
            for (var i = 1; i < 7; i += 2) {
                sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
            }
            if (opacity) {
                return `rgba(${sColorChange.join(",")},${opacity})`;

            } else {
                return "rgb(" + sColorChange.join(",") + ")";

            }
        } else {
            return sColor;
        }
    };
    const cas = document.querySelector('#cas'),
        ctx = cas.getContext('2d')
    const paintFn = { // ÁîªÂõæÁöÑÊñπÊ≥ï
        line(v) { // Ê®™Á∫ø
            if (!v.data1) ctx.strokeStyle = bg_arc
            else ctx.strokeStyle = v.color.colorRgb(.5)
            ctx.beginPath()
            ctx.lineWidth = .01 * doc_fontSize
            ctx.moveTo(2 * x + 5, 2 * (y - r)) //Ê®™Á∫øÁöÑËµ∑ÁÇπ
            ctx.lineTo(2 * (x + line_width + 5), 2 * (y - r)) //Ê®™Á∫øÁöÑÁªàÁÇπ
            ctx.stroke()
        },
        bgArc(sAngle, eAngle) { // ËÉåÊôØÂúÜÂºß
            ctx.beginPath()
            ctx.lineWidth = arc_width
            ctx.strokeStyle = bg_arc
            ctx.arc(2 * x, 2 * y, 2 * r, sAngle, (-2 + d_angle) * Math.PI, true)
            ctx.stroke()
        },
        dataArc(v, sAngle, eAngle) { // Êï∞ÊçÆÂúÜÂºß
            let grd = ctx.createLinearGradient(2 * (x - r), 0, 2 * (x + r), 2 * (y + r));
            grd.addColorStop(0, v.color.colorRgb(1));
            grd.addColorStop(1, v.color.colorRgb(.3));

            if (v.data1) {
                ctx.beginPath()
                ctx.strokeStyle = grd
                ctx.arc(2 * x, 2 * y, 2 * r, sAngle, eAngle, true)
                ctx.stroke()
            }
        },
        iconArc(v) { // ÂõæÊ†áÂúÜ
            if (!v.data1) ctx.strokeStyle = 'rgba(0,0,0,.1)'
            ctx.lineWidth = 2 * 2
            ctx.beginPath()
            ctx.arc(2 * (x + line_width + 10), 2 * (y - r), 2 * 2.5, -.5 * Math.PI, -2.5 * Math.PI, true)
            ctx.stroke()
        },
        className(v) { // Êï∞ÊçÆÂêç
            ctx.beginPath()
            ctx.font = `${.18 * doc_fontSize}px normal PingFangSC-Regular`;
            if (!v.data1) ctx.fillStyle = 'rgba(0,0,0,.1)'
            else ctx.fillStyle = '#000'
            ctx.textAlign = "left"
            ctx.textBaseline = "middle"
            ctx.fillText(v.title, 2 * (x + line_width + 17.5), 2 * (y - r));
        },
        dataOne(v) { // Êï∞ÊçÆÂÄº1
            ctx.beginPath()
            ctx.font = `${.18 * doc_fontSize}px normal PingFangSC-Regular`;
            if (!v.data1) ctx.fillStyle = 'rgba(0,0,0,.1)'
            else ctx.fillStyle = '#000'
            ctx.textAlign = "left"
            ctx.textBaseline = "middle"
            // ctx.fillText(`${v.data1 == 0 ? '' : v.data1}‰∏á`, 2 * (x + line_width + v.title.length * .1 * doc_fontSize + 20), 2 * (y - r));
            ctx.fillText(`${v.data1 == 0 ? '' : v.data1}‰∏á`, 2 * (x + line_width + 45), 2 * (y - r));
        },
        lineTwo(v) { // Èó¥ÈöîÁ∫ø
            if (v.data1) {
                ctx.strokeStyle = 'rgba(0,0,0,.2)'
                ctx.beginPath()
                ctx.lineWidth = 1
                // ctx.moveTo(2 * (x + line_width + v.title.length * .18 * doc_fontSize + (v.data1 + '').length * .1055 * doc_fontSize + 5), 2 * (y - r + 10 / 2 - 12))
                // ctx.lineTo(2 * (x + line_width + v.title.length * .18 * doc_fontSize + (v.data1 + '').length * .1055 * doc_fontSize + 5), 2 * (y - r + 10 / 2))
                ctx.moveTo(2 * (x + line_width + 85), 2 * (y - r + 10 / 2 - 12))
                ctx.lineTo(2 * (x + line_width + 85), 2 * (y - r + 10 / 2))
                ctx.stroke()
            }
        },
        dataTwo(v) { // Êï∞ÊçÆÂÄº2
            if (v.data1) {
                ctx.beginPath()
                ctx.font = `${.18 * doc_fontSize}px normal PingFangSC-Regular`;
                ctx.fillStyle = 'rgba(0,0,0,.5)'
                ctx.textAlign = "left"
                ctx.textBaseline = "middle"
                // ctx.fillText(`Áº∫Âè£${v.data2}‰∏á`, 2 * (x + line_width + v.title.length * .18 * doc_fontSize + (v.data1 + '').length * .1055 * doc_fontSize + 10), 2 * (y - r));
                ctx.fillText(`Áº∫Âè£ ${v.data2}‰∏á`, 2 * (x + line_width + 95), 2 * (y - r));
            }
        }
    }



    let r = 20, // ÂúÜÁöÑÂçäÂæÑ
        r_r = 15, // ÂúÜÂçäÂæÑÁöÑÈÄíÂ¢ûÈáè
        angle = .25, // Ëµ∑ÁÇπÂºßÂ∫¶
        d_angle = .05, //ÂÅèÁßªÂºßÂ∫¶
        x = 0, // ÂàùÂßãÂåñÂúÜÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†áxËΩ¥
        y = 0, // ÂàùÂßãÂåñÂúÜÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†áyËΩ¥
        line_width = 35, //Ê®™Á∫øÁöÑÂÆΩÂ∫¶ (Âçï‰Ωçpx)
        bg_arc = '#F7F7F7', // ËÉåÊôØÂúÜÂºßÁöÑÈ¢úËâ≤
        arc_width = 6 * 2 // ÂúÜÂºßÁöÑÂÆΩÂ∫¶
    const getEndAngle = (data1, data2, angle, deviation_angle) => -(data1 / (data1 + data2) * 1.5 + angle * 2 - deviation_angle) * Math.PI
    let doc_fontSize = 0
    const rem = () => {
        let width = document.body.offsetWidth
        if (width < 320) {
            width = 320
        }
        let htmlFontSize = width / 320 * 100
        doc_fontSize = htmlFontSize
        window.document.documentElement.style.fontSize = `${htmlFontSize}px`
    }
    const canvasFun = arr => {
        r = 20 //ÈáçÁΩÆÂúÜÁöÑÂçäÂæÑ
        d_angle = .05 //ÈáçÁΩÆÂÅèÁßªÂºßÂ∫¶
        cas.width = document.body.offsetWidth * 2 //ÁîªÂ∏ÉÁöÑÂÆΩÂ∫¶
        cas.height = document.body.offsetWidth * 2 //ÁîªÂ∏ÉÁöÑÈ´òÂ∫¶

        document.querySelector('#cas').style.width = '100%'
        
        ctx.lineCap = "round" //ÊãêËßíË°•ÂÅø‰∏∫ÂúÜÂºß
        x = y = (arr.length - 1) * r_r + r + r_r / 2 //ÈÄöËøáÊï∞ÊçÆÈáèËÆ°ÁÆóÂá∫ÁöÑÂúÜÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†áxËΩ¥
        console.log(x, y, r);

        arr.forEach(v => {
            let sAngle = (-.5 + d_angle) * Math.PI,
                eAngle = getEndAngle(v.data1, v.data2, angle, d_angle)
            paintFn.line(v) /* Ê®™Á∫ø */
            paintFn.bgArc(sAngle) /* ËÉåÊôØÂúÜÂºß */
            paintFn.dataArc(v, sAngle, eAngle)/* Êï∞ÊçÆÂúÜÂºß */
            paintFn.iconArc(v) /* Ê®™Á∫øÂêéÁöÑÂúÜ */
            paintFn.className(v) /* ÂêçÁß∞ */
            paintFn.dataOne(v) /* Êï∞ÊçÆ1 */
            paintFn.lineTwo(v) /* Èó¥ÈöîÁ∫ø */
            paintFn.dataTwo(v) /* Êï∞ÊçÆ2 */
            r += r_r
            d_angle += .0005
        })
    }


    rem()
    canvasFun(arr)
    window.addEventListener('resize', () => {
        rem()
        canvasFun(arr)
    })
}